<template>
    <div>
        <div class="archive" v-for="(archive,index) in archives" :key="index" @click="toArticles(archive)">
            <span class="time">· 11.12</span><span>{{archive.title}}</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Archives",
        props: {
            categorie: String
        },
        computed: {
            archives() {
                if (this.categorie) {
                    let articlesTitle = this.$store.state.articlesTitle;
                    let art = [];
                    articlesTitle.map((artic) => {
                        if (artic.categories === this.categorie) {
                            art.push(artic);
                        }
                    });
                    return art;
                }
                return this.$store.state.articlesTitle;
            }
        },
        methods: {
            toArticles(article) {
                this.$router.push({
                    path: `/blog/articles/${article.id}`
                });
            }
        }
    };
</script>

<style lang="scss" scoped>
    .archive {
        margin: 25px 0;
        font-size: 14px;
        cursor: pointer;

        .time {
            display: inline-block;
            font-size: 12px;
            padding-right: 10px;
            color: rgba(0, 0, 0, 0.3);
        }
    }
</style>
